<template>
  <div>
    <el-dialog
      title="处理"
      :visible.sync="wareHouseFormVisible"
    >
      <span class="dialog-title">平仓信息</span>
      <el-form
        :model="formData"
        :inline="true"
        ref="formData"
      >
        <el-form-item
          label="仓单编号"
          :label-width="formLabelWidth"
          style="width:365px;"
        >
          {{form.wareHouseId}}
        </el-form-item>
      </el-form>
      <el-form
        :model="formData"
        :inline="true"
        :rules="rules"
        ref="formData"
      >
        <el-form-item
          label="交易方式"
          :label-width="formLabelWidth"
          style="width:365px;"
          prop="tradeWay"
          v-if="form.status == 0"
        >
          <el-select
            v-model="formData.tradeWay"
            placeholder="请选择交易方式"
          >
            <el-option
              label="回购"
              value="1"
            ></el-option>
            <el-option
              label="抛售"
              value="2"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item
          label="交易方式"
          :label-width="formLabelWidth"
          style="width:365px;"
          v-else
        >
          <el-select
            v-model="form.tradeWay"
            placeholder="请选择交易方式"
            :disabled="(form.status == 1)"
          >
            <el-option
              label="回购"
              value="1"
            ></el-option>
            <el-option
              label="抛售"
              value="2"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item
          label="平仓总价"
          :label-width="formLabelWidth"
          prop="closepositionPrice"
          v-if="form.status == 0"
        >
          <el-input v-model="formData.closepositionPrice"></el-input>
        </el-form-item>
        <el-form-item
          label="平仓总价"
          :label-width="formLabelWidth"
          v-else
        >
          <el-input
            v-model="form.closepositionPrice"
            :disabled="(form.status == 1)"
          ></el-input>
        </el-form-item>
        <el-form-item
          label="平仓时间"
          :label-width="formLabelWidth"
          style="width:365px;"
          prop="closepositionTime"
          v-if="form.status == 0"
        >
          <el-date-picker
            type="datetime"
            placeholder="选择日期时间"
            v-model="formData.closepositionTime"
            value-format="timestamp"
            style="width: 100%;"
          ></el-date-picker>
        </el-form-item>
        <el-form-item
          label="平仓时间"
          :label-width="formLabelWidth"
          style="width:365px;"
          v-else
        >
          <el-date-picker
            type="datetime"
            placeholder="选择日期时间"
            v-model="form.closepositionTime"
            value-format="timestamp"
            style="width: 100%;"
            :disabled="(form.status == 1)"
          ></el-date-picker>
        </el-form-item>
        <el-form-item
          label="平仓时间期货价格"
          :label-width="formLabelWidth"
          prop="closepositionOptionPrice"
          v-if="form.status == 0"
        >
          <el-input v-model="formData.closepositionOptionPrice"></el-input>
        </el-form-item>
        <el-form-item
          label="平仓时间期货价格"
          :label-width="formLabelWidth"
          v-else
        >
          <el-input
            v-model="form.closepositionOptionPrice"
            :disabled="(form.status == 1)"
          ></el-input>
        </el-form-item>
      </el-form>
      <div class="buttonGroup">
        <el-button
          type="primary"
          @click="liquidate('formData', form.status)"
          v-if="(form.status == 0)"
        >确认平仓</el-button>
        <el-button
          type="primary"
          @click="liquidate('formData', form.status)"
          v-else
        >确认还款并归档</el-button>
        <el-button
          type="primary"
          @click="deleteRecord('合同')"
        >删除合同</el-button>
      </div>
      <div
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="wareHouseFormVisible = false">取 消</el-button>
        <!-- <el-button type="primary" @click="dialogFormVisible = false">确 定</el-button> -->
      </div>
    </el-dialog>

    <el-dialog
      title="处理"
      :visible.sync="warningOperateVisible"
    >
      <el-tabs type="border-card">
        <el-tab-pane label="价值预警">
          <table class="value-warning">
            <tr>
              <th>放款金额</th>
              <td>{{tofixed(form.totalLoan)}}</td>
              <!-- <th>&nbsp;</th>
              <td>&nbsp;</td> -->
            </tr>
            <tr>
              <th>仓单原价值</th>
              <td>{{tofixed(form.originalPrice)}}</td>
              <th>仓单现价值</th>
              <td>{{tofixed(form.amount * form.goodsPrice)}}</td>
            </tr>
            <tr>
              <th>原保证金</th>
              <td>{{tofixed(parseInt(form.originalPrice) - parseInt(form.totalLoan))}}</td>
              <th>现保证金</th>
              <td>{{tofixed((parseInt(form.deposit)?parseInt(form.deposit):0) + (parseInt(form.originalPrice) - parseInt(form.totalLoan)))}}</td>
            </tr>
          </table>
        </el-tab-pane>
        <el-tab-pane label="保值方案">
          <div class="program">
            需提醒客户追加{{form.x>0 && isFinite(form.x)?tofixed(form.x):"-"}}吨的货物或{{form.y>0?tofixed(form.y):"-"}}元保证金。
          </div>
        </el-tab-pane>
        <el-tab-pane label="风险处理">
          <div class="program">
            客户实际追加
            <el-input
              v-model="programForm.weight"
              @focus="addWarehouse"
              clearable
              style="width:120px"
            >
            </el-input>
            吨的货物，
            <el-input
              v-model="programForm.realDeposit"
              clearable
              style="width:120px"
            >
            </el-input>
            元保证金。
          </div>
          <el-button
            type="primary"
            class="dialog-button"
            @click="submitForm"
          >确定</el-button>
        </el-tab-pane>
      </el-tabs>
    </el-dialog>

    <el-dialog
      title="追加仓单："
      :visible.sync="warehouseInnerVisible"
      width="1200px"
      :close-on-click-modal="false"
      append-to-body
    >
      <div class="tableDate">
        <div
          class="button"
          style="width:3%;float:right;"
        >
          <P>
            <el-button
              class="el-icon-plus"
              @click.prevent="addRow()"
            ></el-button>
          </P>
          <p>
            <el-button
              class="el-icon-minus"
              @click.prevent="delData()"
            ></el-button>
          </p>
        </div>
        <div class="table">
          <el-table
            :data="addWareHouseTable"
            ref="addWareHouseTable"
            tooltip-effect="dark"
            border
            stripe
            style="width: 95%"
            @selection-change="selectRow"
          >
            <el-table-column
              type="selection"
              width="45"
              align="center"
            ></el-table-column>
            <el-table-column
              label="仓单编号"
              align="center"
              width="195"
            >
              <template slot-scope="scope">
                <el-input v-model="scope.row.wwNo"></el-input>
              </template>
            </el-table-column>
            <el-table-column
              label="商品名称"
              width="90"
            >
              <template slot-scope="scope">
                <el-input v-model="scope.row.goodsName"></el-input>
              </template>
            </el-table-column>
            <el-table-column
              label="品牌/牌号"
              width="100"
            >
              <template slot-scope="scope">
                <el-input v-model="scope.row.brand"></el-input>
              </template>
            </el-table-column>
            <el-table-column
              label="质量标准"
              width="110"
            >
              <template slot-scope="scope">
                <el-input v-model="scope.row.qualityStandard"></el-input>
              </template>
            </el-table-column>
            <el-table-column
              label="包装规格"
              width="115"
            >
              <template slot-scope="scope">
                <el-input v-model="scope.row.packingSpecification"></el-input>
              </template>
            </el-table-column>
            <el-table-column
              label="数量"
              width="105"
            >
              <template slot-scope="scope">
                <el-input v-model="scope.row.quantity"></el-input>
              </template>
            </el-table-column>
            <el-table-column
              label="计量单位"
              width="80"
            >
              <template slot-scope="scope">
                <el-input v-model="scope.row.unit"></el-input>
              </template>
            </el-table-column>
            <el-table-column
              label="仓库名称"
              width="115"
            >
              <template slot-scope="scope">
                <el-input v-model="scope.row.warehouseName"></el-input>
              </template>
            </el-table-column>
            <el-table-column
              label="仓库地址"
              width="205"
            >
              <template slot-scope="scope">
                <el-input v-model="scope.row.warehouseAddress"></el-input>
              </template>
            </el-table-column>
            <el-table-column
              label="联系人"
              width="115"
            >
              <template slot-scope="scope">
                <el-input v-model="scope.row.linkman"></el-input>
              </template>
            </el-table-column>
            <el-table-column
              label="联系电话"
              width="165"
            >
              <template slot-scope="scope">
                <el-input v-model="scope.row.linkPhone"></el-input>
              </template>
            </el-table-column>
            <el-table-column
              label="货物位置"
              width="165"
            >
              <template slot-scope="scope">
                <el-input v-model="scope.row.goodsAddress"></el-input>
              </template>
            </el-table-column>
            <el-table-column
              label="货物真实性"
              width="115"
            >
              <template slot-scope="scope">
                <el-input v-model="scope.row.goodsAuthenticity"></el-input>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </div>
      <div
        slot="footer"
        class="dialog-footer"
      >
        <el-button
          type="primary"
          @click="addWareHouseTableToProgram"
        >确 定</el-button>
        <!-- <el-button type="primary" @click="dialogFormVisible = false">确 定</el-button> -->
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { rules } from "../validate.js";
import * as ajaxUrl from "../ajax.config.js";

export default {
  props: ["dialogFormVisible", "form", "dialogFormVisible1", "index"],
  data() {
    return {
      formData: {},
      programForm: {
        weight: "",
        realDeposit: ""
      },
      selectlistRow: [],
      addWareHouseTable: [], //追加仓单信息
      keepValuePlan: {},

      wareHouseFormVisible: this.dialogFormVisible,
      warningOperateVisible: this.dialogFormVisible1,
      warehouseInnerVisible: false,
      formLabelWidth: "135px",
      rules: rules,

      liquidated: false //判断是否平仓
    };
  },
  watch: {
    dialogFormVisible(val) {
      this.wareHouseFormVisible = val; //新增result的watch，监听变更并同步到myResult上
    },
    dialogFormVisible1(val) {
      this.warningOperateVisible = val;
    },
    wareHouseFormVisible(val) {
      this.$emit("dialogFormVisibleChange", val); //③组件内对dialogFormVisibleChange变更后向外部发送事件通知
    },
    warningOperateVisible(val) {
      this.$emit("warningOperateVisibleChange", val); //③组件内对dialogFormVisibleChange变更后向外部发送事件通知
    }
  },
  methods: {
    liquidate(formData, status) {
      this.$refs[formData].validate(valid => {
        if (valid && status == 0) {
          this.formData.status = parseInt(status + 1);
          this.$confirm("是否平仓?", "提示", {
            confirmButtonText: "确定",
            cancelButtonText: "取消",
            type: "warning"
          })
            .then(() => {
              this.axios
                .put(
                  ajaxUrl.HOLDWARRANTCONFIRM + "/" + (this.index == true ? this.form.formId : this.form.id),
                  this.formData
                )
                .then(res => {
                  if(res.data.resCode == 200) {
                    this.$message({
                      type: "success",
                      message: "已平仓!"
                    });
                    this.wareHouseFormVisible = false;
                    this.formData = {};
                    this.$router.push("/Index");
                    this.$emit("searchInfo");
                  }else{
                    this.$message.error(res.data.resMsg)
                  }
                })
                .catch(err => {
                  console.log(err);
                });
            })
            .catch(() => {
              this.$message({
                type: "info",
                message: "已取消平仓"
              });
            });
        } else if (status == 1) {
          this.formData.tradeWay = this.form.tradeWay;
          this.formData.closepositionPrice = this.form.closepositionPrice;
          this.formData.closepositionTime = this.form.closepositionTime;
          this.formData.closepositionOptionPrice = this.form.closepositionOptionPrice;
          this.formData.status = parseInt(status + 1);
          this.$confirm("是否归档?", "提示", {
            confirmButtonText: "确定",
            cancelButtonText: "取消",
            type: "warning"
          })
            .then(() => {
              this.axios
                .put(ajaxUrl.HOLDWARRANTCONFIRM + "/" + (this.index == true ? this.form.formId : this.form.id), this.formData)
                .then(res => {
                  if(res.data.resCode == 200) {
                    this.$message({
                    type: "success",
                    message: "已归档!"
                    });
                    this.deleteOperateRecord(this.form.id);
                    this.wareHouseFormVisible = false;
                    this.formData = {};
                    this.$router.push("/Index");
                    this.$emit("searchInfo");
                    }else{
                    this.$message.error(res.data.resMsg)
                  }
                })
                .catch(err => {
                  console.log(err);
                });
            })
            .catch(() => {
              this.$message({
                type: "info",
                message: "已取消平仓"
              });
            });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    deleteRecord(type) {
      this.$emit("deleteRecord", type);
    },
    deleteOperateRecord(id) {
      this.axios
        .delete(ajaxUrl.DELETEINDEXINFO + "/" + id)
        .then(res => {
          if(res.data.resCode == 200) {
            this.$emit("searchInfo");
          }else{
            this.$message.error(res.data.resMsg)
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    addWarehouse() {
      this.warehouseInnerVisible = true;
    },
    // 增加行
    addRow() {
      let list = {
        rowNum: this.addWareHouseTable.length + 1,
        wwNo: this.wwNo,
        goodsName: this.goodsName,
        brand: this.brand,
        qualityStandard: this.qualityStandard,
        packingSpecification: this.packingSpecification,
        quantity: this.quantity,
        unit: this.unit,
        warehouseName: this.warehouseName,
        warehouseAddress: this.warehouseAddress,
        linkman: this.linkman,
        linkPhone: this.linkPhone,
        goodsAddress: this.goodsAddress,
        goodsAuthenticity: this.goodsAuthenticity
      };
      this.addWareHouseTable.unshift(list);
    },
    // 删除选中行
    delData() {
      for (let i = 0; i < this.selectlistRow.length; i++) {
        let val = this.selectlistRow;
        // 获取选中行的索引的方法
        // 遍历表格中tableData数据和选中的val数据，比较它们的rowNum,相等则输出选中行的索引
        // rowNum的作用主要是为了让每一行有一个唯一的数据，方便比较，可以根据个人的开发需求从后台传入特定的数据
        val.forEach((val, index) => {
          this.addWareHouseTable.forEach((v, i) => {
            if (val.rowNum === v.rowNum) {
              // i 为选中的索引
              this.addWareHouseTable.splice(i, 1);
            }
          });
        });
      }
      // 删除完数据之后清除勾选框
      this.$refs.addWareHouseTable.clearSelection();
    },
    // 获取表格选中时的数据
    selectRow(val) {
      this.selectlistRow = val;
    },

    addWareHouseTableToProgram() {
      let list = this.addWareHouseTable;
      let weight = 0;
      for (let i = 0; i < list.length; i++) {
        if (parseInt(list[i].quantity)) {
          weight = weight + parseInt(list[i].quantity);
        }
      }
      this.programForm.weight = weight;
      this.programForm.warehouseWarrantList = this.addWareHouseTable;
      this.warehouseInnerVisible = false;
    },
    submitForm() {

      this.programForm.contractId = this.form.contractId;
      this.programForm.warningPrice = this.form.warningPrice;
      this.programForm.totalPrice = this.form.totalPrice;
      this.programForm.contractDeposit = this.form.deposit;
      this.programForm.id = this.form.formId;

      this.axios
        .post(ajaxUrl.WARNINGINFODEAL, this.programForm)
        .then(res => {
          if(res.data.resCode == 200){
            this.$message({
              message: res.data.resMsg,
              type: "success"
            });
            this.$router.push("/Index");
            this.$emit("searchInfo");
            this.warningOperateVisible = false
            this.programForm = {};
          }else{
            this.$message.error(res.data.resMsg)
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    tofixed(number) {
      return this.common.toFixed(number);
    }
  },
  created() {},
  mounted() {},
  components: {}
};
</script>

<style>
.delete-contract {
  display: block;
  margin-bottom: 15px;
  width: 85px;
}
.delete-contract:hover {
  cursor: pointer;
  border-bottom: 1px solid black;
}
.value-warning {
  border-spacing: 0;
  border-collapse: collapse;
}
.value-warning tr th {
  width: 150px;
  height: 70px;
  border: 1px solid gray;
}
.value-warning tr td {
  width: 200px;
  height: 70px;
  text-align: center;
  border: 1px solid gray;
}
.program {
  height: 100px;
}
.dialog-button {
  float: right;
}
</style>



